@import '../../../../styles/mixins/global.scss';

.herzlich-section-banner {
    background-color: var(--light-background);
    padding: 63px 0;

    @include breakpoint("max-sm") {
        padding: 60px 0;
    }

    .mobile-view-some-image-show {
        display: none;
        padding: 0 0 16px 0;

        @include breakpoint("max-sm") {
            display: none;
        }
    }

    .grid {
        display: grid;
        grid-template-columns: 1fr 400px;
        gap: 128px;
        align-items: center;

        @include breakpoint("max-md") {
            gap: 40px;
        }

        @include breakpoint("max-sm") {
            grid-template-columns: repeat(1, 1fr);
            gap: 30px;
        }


        .grid-items:nth-child(1) {
            @include breakpoint("max-sm") {
                order: 2;
            }
        }

        .grid-items:nth-child(2) {
            @include breakpoint("max-sm") {
                order: 1;
            }
        }

        .grid-items {


            .image-style {
                img {
                    width: 100%;
                    height: 540px;
                    object-fit: cover;

                    @include breakpoint("max-sm") {
                        height: 455px;
                        display: none;
                    }
                }
            }

            p {
                @include text-sm;
                color: var(--text-white);
                margin: 40px 0 20px 0;
                max-width: 425px;

                @include breakpoint("max-md") {
                    margin: 40px 0 28px 0;
                }

                @include breakpoint("max-sm") {
                    @include mobile-12;
                    margin: 30px 0 28px 0;
                }
            }

            .button-alignment {

                button:last-child {
                    margin: 0;
                }

                a {
                    padding: 0 0 20px 0;
                    display: block;
                }

                button {
                    padding: 8px 36px;
                    border: 1px solid var(--text-white);
                    color: var(--text-white);
                    background-color: transparent;
                    @include text-sm;
                    display: block;
                    margin: 0 0 20px 0;
                    @include pointer;
                    text-transform: uppercase;
                    transition: 0.3s ease-in-out;

                    &:hover {
                        background-color: #fff;
                        color: var(--text-black);
                    }

                    @include breakpoint("max-md") {
                        @include mobile-10;
                    }
                }
            }

            h1 {
                @include heading-1;
                color: var(--text-white);
                position: relative;
                text-transform: uppercase;

                @include breakpoint("max-md") {
                    @include heading-md;
                }

                @include breakpoint("max-sm") {
                    text-transform: uppercase;
                }

                &::before {
                    position: absolute;
                    bottom: -15px;
                    width: 34px;
                    height: 1px;
                    background-color: var(--text-white);
                    content: "";
                    left: 0;
                }
            }
        }
    }
}