@import '../../../../styles/mixins/global.scss';

.home-information-all-section-content-alignment {
    max-width: 100%;

    .grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 5px;

        @include breakpoint("max-sm") {
            grid-template-columns: repeat(1, 1fr);
            gap: 0;
        }

        .grid-items {
            position: relative;

            .card-image {
                img {
                    width: 100%;
                    height: 961px;
                    object-position: center;
                    object-fit: cover;

                    @include breakpoint("max-md") {
                        height: 630px;
                    }

                    @include breakpoint("max-sm") {
                        height: 760px;
                    }
                }
            }

            .card-details-content-alignment {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                align-items: flex-end;

                .card-spacing-alignment {
                    padding: 54px;

                    @include breakpoint("max-md") {
                        padding: 32px 16px;
                    }

                    @include breakpoint("max-sm") {
                        padding: 31px;
                    }

                    .button-alignment {
                        button {
                            padding: 8px 36px;
                            border: 1px solid var(--text-white);
                            color: var(--text-white);
                            background-color: transparent;
                            @include text-sm;
                            display: block;
                            margin: 0 0 20px 0;
                            @include pointer;
                            text-transform: uppercase;
                            transition: .3s ease-in-out;

                            &:hover {
                                background-color: #fff;
                                color: var(--text-black);
                            }

                            @include breakpoint("max-sm") {
                                padding: 8px 20px;
                                @include mobile-10;
                            }
                        }
                    }

                    .child-all-content-alignment {
                        padding: 40px 0 72px 0;
                        height: 396px;

                        @include breakpoint("max-xl") {
                            height: 460px;
                        }

                        @include breakpoint("max-md") {
                            padding: 40px 0;
                            height: 380px;
                        }

                        @include breakpoint("max-sm") {
                            height: auto;
                        }

                        p {
                            @include text-sm;
                            color: var(--text-white);
                            font-weight: 500;
                            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
                            margin: 0 0 25px 0;

                            @include breakpoint("max-md") {
                                @include mobile-10;
                                text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
                                margin: 0 0 14px 0;
                            }

                            @include breakpoint("max-sm") {
                                @include mobile-11;
                            }
                        }



                        p:last-child {
                            margin: 0;
                        }
                    }

                    h2 {
                        @include heading-1;
                        color: var(--text-white);
                        position: relative;
                        text-transform: uppercase;

                        @include breakpoint("max-md") {
                            @include heading-md;
                        }

                        &::before {
                            position: absolute;
                            bottom: -15px;
                            width: 34px;
                            height: 1px;
                            background-color: var(--text-white);
                            content: "";
                            left: 0;
                        }
                    }
                }
            }
        }
    }
}